export function getUvIndex(userLang:string, uvIndex: number):string {
    if(uvIndex === 0||1||2){
        if(userLang === 'ru'){
            return ''
        }else {
            return 'Low'
        }
    }else if(uvIndex === 3||4||5){
       if(userLang === 'ru'){
            return ''
        }else {
            return 'Moderate'
        }
    }else if(uvIndex === 6||7){
       if(userLang === 'ru'){
            return ''
        }else {
            return 'High'
        }
    }else if(uvIndex === 8||9||10){
       if(userLang === 'ru'){
            return ' \t'
        }else {
            return 'Very high'
        }
    }else{
       if(userLang === 'ru'){
            return ''
        }else {
            return 'Extreme'
        }
    }
}